'use client'
import { useEffect, useState } from "react"

export default function ShootsPage() {
  const [shoots, setShoots] = useState([]);
  const [error, setError] = useState(null);

  useEffect(() => {
    const fetchShoots = async () => {
      const res = await fetch('/api/shoots/get-shoots');
      const { data, error } = await res.json();
      if (error) setError(error);
      else setShoots(data || []);
    };
    fetchShoots();
  }, []);

  return (
    <div style={{ padding: "40px 0", minHeight: "100vh", background: "#f8fafc" }}>
      <header style={{ marginBottom: "32px" }}>
        <h1 style={{ fontSize: "2rem", fontWeight: "bold", color: "#0070f3" }}>Shoots</h1>
      </header>

      {error && <p style={{ color: "red" }}>{error}</p>}

      <div style={{
        display: "flex",
        flexWrap: "wrap",
        gap: "32px",
        justifyContent: "flex-start",
      }}>
        {shoots && shoots.length > 0 ? (
          shoots.map((shoot, idx) => (
            <div
              key={shoot.id || idx}
              style={{
                width: "180px",
                height: "180px",
                borderRadius: "50%",
                background: "#fff",
                boxShadow: "0 2px 12px rgba(0,0,0,0.08)",
                display: "flex",
                flexDirection: "column",
                alignItems: "center",
                justifyContent: "center",
                textAlign: "center",
                border: "4px solid #0070f3",
                transition: "box-shadow 0.2s",
              }}
            >
              <div style={{ fontWeight: "bold", fontSize: "1.1rem", color: "#0070f3", marginBottom: "8px" }}>
                {shoot.client || "No Client"}
              </div>
              <div style={{ color: "#333", fontSize: "1rem", marginBottom: "8px" }}>
                {shoot.date ? new Date(shoot.date).toLocaleDateString() : "No Date"}
              </div>
              <div style={{ color: "#555", fontSize: "1rem" }}>
                {shoot.genre || "No Genre"}
              </div>
            </div>
          ))
        ) : (
          <p>No shoots found.</p>
        )}
      </div>
    </div>
  );
}